html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
}

.nav__list {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-weight: 500;
  cursor: pointer;
}

.nav__item {
  margin: 0 10px  ;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.images {
  margin-top: 10px;
}

.nav__link {
  text-decoration: none;
  padding: 22.5px 0 23px ;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  position: relative;
  color: #00acdc;
}

.is-active::after {
  position: absolute;
  bottom: 0;
  left: 0;
  background: #00acdc;
  width: 100%;
  height: 4px;
  content: "";
  border-radius: 8px;
}
